// -- vars
$default-size: 55px;
// -- mixins
@mixin size($width, $height) {
  height: $height;
  width: $width;
}
@mixin draw-progress($progress) {
  .pie {
    .half-circle {
      border-color: $component-color2;
    }

    .left-side {
      transform: rotate($progress * 3.6deg);
    }

    @if $progress <= 50 {
      .right-side {
        display: none;
      }
    } @else {
      clip: rect(auto, auto, auto, auto);

      .right-side {
        transform: rotate(180deg);
      }
    }
  }
}

.game__modal {
  padding-right: 0 !important;
  background-color: $component-color1;
  &-dialog {
    max-width: 100%;
    margin: 0;
    @media #{$breakpoint-xl} {
      margin: 20px;
    }
  }
  &-content {
    height: 100vh;
    border: 1px solid transparent;
    background: $white;
    overflow: auto;
    @media #{$breakpoint-xl} {
      border-radius: 30px;
      height: calc(100vh - 40px);
    }
    &::-webkit-scrollbar {
      width: 8px;
      &-track {
        background-color: $component-color1;
        border-radius: 10px;
      }
      &-thumb {
        background: $component-color2;
        border-radius: 10px;
        &:hover {
          background: $component-color2;
        }
      }
    }
  }
  &-header {
    display: flex;
    justify-content: flex-end;
    padding: 10px 15px 0;
    @media #{$breakpoint-md} {
      padding: 34px 34px 0;
    }
    &.between {
      justify-content: space-between;
    }
  }
  &-body {
    background-color: $white;
    max-width: 100%;
    margin: auto;
    padding: 55px 30px 30px 30px;
    @media #{$breakpoint-lg} {
      max-width: 1024px;
    }
    @media #{$breakpoint-xl} {
      max-width: 1326px;
    }
    @media #{$breakpoint-xl-ex} {
      max-width: 1880px;
    }
  }
  &-close {
    width: 40px;
    height: 40px;
    border: 1px solid $component-color2;
    background-color: $white;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s;
    z-index: 1;
    flex: none;
    &.center {
      position: absolute;
      left: 50%;
      top: -20px;
      transform: translateX(-50%);
    }
    .icon {
      width: 8px;
      height: 8px;
      transition: 0.3s;
    }
    &:hover {
      background-color: $component-color2;
      .icon {
        fill: $white;
      }
    }
    &.dark {
      background-color: $component-color2;
      .icon {
        fill: $white;
      }
      &:hover {
        background-color: $component-color2-darken;
      }
    }
  }
}

.game {
  &__text {
    font-family: $firagoRegular;
    color: $black;
    margin-bottom: 0;
    &.lg {
      font-size: 21px;
    }
    &.md {
      font-size: 16px;
    }
    &.mb-34 {
      margin-bottom: 34px;
    }
    &.light{
      color: rgba($color: $black, $alpha: 0.6);
    }
  }
  &__title {
    font-family: $firagoBold;
    font-size: 21px;
    color: $black;
    margin-bottom: 0;
    &.mb-34 {
      margin-bottom: 34px;
    }
    &.lg {
      @media #{$breakpoint-md} {
        font-size: 34px;
      }
    }
  }
  &__answer {
    &__drop {
      &__container {
        border: 2px dashed $component-color10;
        border-radius: 16px;
        width: 100%;
        padding: 10px;
        position: relative;
        min-height: 65px;
        &__placeholder {
          font-family: $firagoMedium;
          font-size: 16px;
          color: rgba($color: $black, $alpha: 0.3);
          margin: 0;
          position: absolute;
          z-index: 0;
          left: 15px;
          top: 50%;
          transform: translateY(-50%);
        }
        .game__answer__container {
          border-radius: 16px;
          background-color: $component-color2;
          margin-bottom: 0;
          position: relative;
          z-index: 1;
          .game__text {
            color: $white;
          }

          &.blue {
            margin: 0;
            background-color: $component-color6;
            &:hover {
              background-color: darken($component-color6, 5%);
              border-color: darken($component-color6, 5%);
            }
          }
          &.error {
            background-color: $error-red;
            border: 2px solid $error-red;
            &:hover {
              background-color: darken($error-red, 5%);
              border-color: darken($error-red, 5%);
            }
          }
          &.success {
            background-color: $component-color9;
            border: 2px solid $component-color9;
            &:hover {
              background-color: darken($component-color9, 5%);
              border-color: darken($component-color9, 5%);
            }
          }
        }
      }
    }
    &__container {
      border: 2px solid $component-color11;
      border-radius: 10px;
      padding: 10px;
      cursor: pointer;
      margin-bottom: 10px;
      transition: background-color 0.3s;
      background-color: $component-color1;
      @media #{$breakpoint-lg} {
        margin-bottom: 20px;
      }
      &:hover {
        background-color: $component-color2;
        .game__text {
          color: $white;
        }
      }
      &.mr {
        margin-right: 20px;
      }
      &.blue {
        border: 2px solid $component-color6;
        &:hover {
          background-color: $component-color6;
        }
      }
      &.definition {
        position: relative;
        .definition__content {
          display: none;
          background-color: $component-color2;
          position: absolute;
          width: 100%;
          min-width: 200px;
          box-shadow: 0px 10px 20px #7279ff1a;
          font-family: $firagoRegular;
          color: $white;
          font-size: 13px;
          padding: 10px;
          z-index: 1;
          border-radius: 5px;
          margin-top: 25px;
          left: 0;
          &::after {
            content: "";
            background-color: $component-color2;
            width: 12px;
            height: 12px;
            position: absolute;
            left: 15px;
            top: -5px;
            transform: rotate(45deg);
          }
          @media #{$breakpoint-xxs} {
            min-width: 280px;
            left: 0;
          }
          @media #{$breakpoint-lg} {
            min-width: 300px;
          }
          @media #{$breakpoint-xl-ex} {
            min-width: 500px;
          }
          .bold {
            font-family: $firagoBold;
          }
        }
        // .game__text{
        //   color: $component-color2;
        // }
        &:hover {
          .game__text {
            color: $white;
          }
        }
      }
    }
  }
  &__line {
    width: 100%;
    height: 1px;
    background-color: $component-color3;
    margin: 34px 0;
    @media #{$breakpoint-lg} {
      margin: 55px 0 34px;
    }
  }
  &__img {
    &__col-relative {
      position: relative;
      display: flex;
      justify-content: center;
      @media #{$breakpoint-md} {
        display: block;
      }
    }
    &__picture {
      position: static;
      @media #{$breakpoint-xl} {
        position: absolute;
