      }
      &-modal-text {
        font-family: $firagoMedium;
        font-size: 16px;
        color: rgba($color: $black, $alpha: 0.6);
      }
    }
    &__answ {
      padding: 20px 10px;
      border-radius: 10px;
      display: flex;
      align-items: center;
      margin-bottom: 10px;
      cursor: pointer;
      @media #{$breakpoint-md} {
        padding: 20px;
      }
      &.blue {
        background-color: $component-color16;
      }
      &.yellow {
        background-color: $component-color17;
      }
      &.pink {
        background-color: $component-color18;
      }
      &.purple {
        background-color: $component-color19;
      }
      .text {
        font-family: $firagoMedium;
        font-size: 16px;
        color: $black;
      }
      &.checked {
        .game__checkbox {
          background-color: $component-color2;
          position: relative;
          &::after {
            content: url("../img/icons/check.svg");
            display: block;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
        }
        // &.success {
        //   background-color: $component-color9;
        //   .text {
        //     color: $white;
        //   }
        // }
        // &.error {
        //   background-color: $error-red;
        //   .text {
        //     color: $white;
        //   }
        // }
      }
      &.success {
        &.active {
          background-color: $component-color9;
          .text {
            color: $white;
          }
        }
      }

      &__cont {
        &.answered-suc {
          .game__quiz__answ:not(.success) {
            opacity: 0.2;
          }
          .success-border {
            border: 2px solid $component-color9;
            opacity: 1 !important;
            &.blue {
              background-color: rgba($color: $component-color16, $alpha: 0.2);
            }
            &.yellow {
              background-color: rgba($color: $component-color17, $alpha: 0.2);
            }
            &.pink {
              background-color: rgba($color: $component-color18, $alpha: 0.2);
            }
            &.purple {
              background-color: rgba($color: $component-color19, $alpha: 0.2);
            }
          }
        }
        &.answered-err {
          .game__quiz__answ:not(.success):not(.error) {
            opacity: 0.2;
          }
        }
      }
    }
    &__img{
      width: 100%;
      max-height: 406px;
      object-fit: contain;
      margin: 20px auto;
      @media #{$breakpoint-md} {
        margin: 0 auto 20px;
        max-height: 506px;
      }
      @media #{$breakpoint-xxl} {
        max-height: 606px;
      }
    }
  }
  &__checkbox {
    width: 20px;
    height: 20px;
    border-radius: 30px;
    background-color: $white;
    border: 1px solid $component-color2;
    margin-right: 10px;
    flex: none;
    @media #{$breakpoint-md} {
      margin-right: 20px;
    }
  }
  &__sentence {
    &__header {
      width: 100%;
      text-align: center;
      padding: 17px;
      border-radius: 10px;
      color: $white;
      font-family: $firagoMedium;
      font-size: 21px;
      &.orange {
        background-color: $component-color8;
      }
      &.blue {
        background-color: $component-color6;
      }
      &.pink {
        background-color: #ff97bb;
      }
    }
    &__cont {
      border-bottom: 1px solid $component-color3;
      margin-top: 34px;
      &__list {
        margin: auto;
        @media #{$breakpoint-lg} {
          max-width: 400px;
        }
        @media #{$breakpoint-xl} {
          max-width: 420px;
        }
        @media #{$breakpoint-xl-ex} {
          max-width: 466px;
        }
      }
      &--border {
        @media #{$breakpoint-md} {
          border-right: 2px dashed $component-color3;
        }
      }
    }
    &__title {
      font-family: $firagoMedium;
      font-size: 21px;
      color: $black;
      margin-bottom: 21px;
    }
    &__input {
      width: 100%;
      border: 2px dashed $component-color10;
      border-radius: 16px;
      padding: 23px 30px;
      outline: none;
      font-family: $firagoMedium;
      font-size: 16px;
      margin-bottom: 34px;
      &::placeholder {
        color: rgba($color: $black, $alpha: 0.3);
      }
      &:focus::placeholder {
        color: transparent;
      }
    }
  }
  &__percent {
    border: 2px solid $component-color12;
    border-radius: 10px;
    padding: 100px 0 55px;
    margin-bottom: 34px;
    &__title {
      font-family: $firagoRegular;
      font-size: 21px;
      color: $component-color2;
      margin-bottom: 34px;
    }
    &__line {
      &__cont {
        width: 90%;
        max-width: 804px;
        margin: auto;
      }
      width: 100%;
      height: 3px;
      background-color: $component-color2;
      position: relative;
    }
    &-active {
      position: absolute;
      top: -14px;
      left: 50%;
      transform: translate(-50%, -50%);
      display: flex;
      flex-direction: column;
      align-items: center;
      &-circle {
        width: 20px;
        height: 20px;
        border-radius: 50%;
        border: 2px solid $component-color20;
        background-color: $component-color9;
        cursor: pointer;
      }
      &-numb {
        color: $component-color9;
        font-size: 21px;
        font-family: $firagoMedium;
        display: block;
      }
    }
    &__numb {
      margin-top: 20px;
      font-family: $firagoMedium;
      font-size: 16px;
      color: rgba($color: $black, $alpha: 0.6);
    }
    &__answer {
      margin-top: 68px;
      color: $black;
      font-size: 21px;
      font-family: $firagoRegular;
      text-align: center;
      @media #{$breakpoint-md} {
        margin-top: 86px;
      }
      .purple {
        display: block;
        color: $component-color2;
        font-family: $firagoMedium;
        font-size: 34px;
      }
      &-incorrect {
        font-family: $firagoRegular;
        font-size: 16px;
        color: $component-color5;
        text-align: center;
        margin-top: 34px;
        .red {
          color: $error-red;
          display: block;
        }
      }
    }
    &__btn {
      padding: 10px 0;
      width: 90%;
      text-align: center;
      background-color: $component-color6;
      border-radius: 8px;
      font-family: $firagoMedium;
      font-size: 16px;
      color: $white;
      margin-top: 34px;
      cursor: pointer;
      @media #{$breakpoint-md} {
        padding: 10px 48px;
        width: auto;
      }
    }
  }
  &__wishlist{
    &__item{
      position: relative;
      width: 100%;
      height: 100%;
      border-radius: 10px;
      overflow: hidden;
      cursor: pointer;
      background-color: $component-color22;
      
      &.active{
        border: 2px solid $component-color9;
        background-color: $component-color23;
        &::after{
          content: url("../img/icons/check.svg");
          background-color: $component-color9;
          border-radius: 50%;
          width: 22px;
          height: 22px;
          position: absolute;
          right: 20px;
          top: 20px;
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }
      &__write{
        width: 100%;
        height: 100%;
        min-height: 252px;
        textarea{
          width: 100%;
          height: 100%;
          outline: none;
          position: relative;
          padding: 20px;
          color: $black;
          font-family: $firagoRegular;
          font-size: 21px;
          resize: none;
          &::placeholder {
            color: $component-color12;
            font-size: 21px;
            font-family: $firagoRegular;
            text-align: center;
            line-height:212px;
          }
          &:focus{
            &::placeholder{
              opacity: 0;
            }
          }
        }
        // .game__box__body{
        //   min-height: auto;
        //   height: 100%;
        //   border: none;
        //   border-radius: 0;
        // }
      }
      &-margin{
        margin-bottom: 30px;
      }
    }
    &__img{
      width: 100%;
    }
  }
}

