.swiper {
  &-container {
    &.drop-swiper-container{
      position: relative;
      width: 98%;
      @media #{$breakpoint-md} {
        width: 100%;
      }
      @media #{$breakpoint-lg} {
        width: 918px;
      }
      @media #{$breakpoint-xl} {
        width: 1158px;
      }
      @media #{$breakpoint-xl-ex} {
        width: 1158px;
      }
      @media #{$breakpoint-xxl} {
        width: 1638px;
      }
  
      & > .swiper-slide__content {
        position: absolute;
        top: 0;
      }
      .swiper-slide {
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all 200ms linear;
        transform: scale(0.8);
        opacity: 0.2;
        position: relative;
        margin-bottom: 62px;
        &.swiper-slide-active {
          transform: scale(1);
          opacity: 1;
        }
        @media #{$breakpoint-md} {
          height: 420px;
          margin-bottom: 0;
        }
        @media #{$breakpoint-lg} {
          height: 458px;
        }
        @media #{$breakpoint-xl} {
          height: 484px;
        }
        @media #{$breakpoint-xxl} {
          height: 660px;
        }
        &-img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
        &-drop {
          width: 60px;
          height: 93px;
          position: absolute;
          bottom: 10px;
          left: 10px;
          backdrop-filter: blur(15px) brightness(150%);
          border: 2px solid $white;
          @media only screen and (min-width: 400px) {
            width: 93px;
            height: 130px;
          }
          @media #{$breakpoint-sm} {
            width: 135px;
            height: 172px;
          }
          @media #{$breakpoint-md} {
            width: 66px;
            height: 103px;
          }
          @media #{$breakpoint-lg} {
            width: 73px;
            height: 112px;
          }
          @media #{$breakpoint-xl} {
            width: 76px;
            height: 119px;
          }
          @media #{$breakpoint-xxl} {
            width: 104px;
            height: 163px;
          }
          &.success {
            border: 5px solid $component-color9;
            &::after {
              content: url("../img/icons/check.svg");
              background-color: $component-color9;
              border-radius: 50%;
              width: 30px;
              height: 30px;
              position: absolute;
              left: 50%;
              transform: translateX(-50%);
              top: -18px;
              display: flex;
              justify-content: center;
              align-items: center;
            }
          }
          &.error {
            border: 5px solid $error-red;
            &::after {
              content: url("../img/icons/close.svg");
              background-color: $error-red;
              border-radius: 50%;
              width: 30px;
              height: 30px;
              position: absolute;
              left: 50%;
              transform: translateX(-50%);
              top: -18px;
              display: flex;
              justify-content: center;
              align-items: center;
            }
          }
          .age__categories__item {
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
  &-button {
    fill: $component-color2;
    @media #{$breakpoint-xxl} {
      width: 46px;
    }
    &::after {
      content: none;
    }
    &-prev {
      transform: rotate(180deg) translate(-50%);
      bottom: 15px;
      top: auto;
      left: 30%;
      outline: none;
      @media #{$breakpoint-md} {
        bottom: auto;
        top: 50%;
        left: 27%;
      }
      @media #{$breakpoint-lg} {
        left: 30%;
      }
    }
    &-next {
      transform: translateX(-50%);
      bottom: 15px;
      top: auto;
      right: 30%;
      outline: none;
      @media #{$breakpoint-md} {
        bottom: auto;
        top: 50%;
        right: 27%;
      }
      @media #{$breakpoint-lg} {
        right: 30%;
      }
    }
  }
}

.phishing-swiper-container{
  .swiper{
    &-button {
      &-prev {
        transform: rotate(180deg) translate(-50%);
        position: static;
        outline: none;
        margin-right: 34px;
        margin-top: 34px;
        @media #{$breakpoint-md} {
          position: absolute;
          bottom: auto;
          top: 50%;
          left: 0;
          margin-right: 0;
          margin-top: 0;
        }
      }
      &-next {
        transform: translateX(-50%);
        position: static;
        outline: none;
        margin-left: 34px;
        margin-top: 34px;
        @media #{$breakpoint-md} {
          position: absolute;
          bottom: auto;
          top: 50%;
          right: 0;
          margin-left: 0;
          margin-top: 0;
        }
      }
    }
    &-slide-img{
      width: 100%;
      object-fit: contain;
      @media #{$breakpoint-md} {
        width: 90%;
        margin: auto;
        display: block;
      }
    }
  }
}

.age__categories {
  display: flex;
  justify-content: space-between;
  overflow-x: auto;
  @media #{$breakpoint-lg} {
    overflow-x: visible;
  }
  &__item {
    height: 55px;
    margin-right: 37px;
    object-fit: contain;
    &:last-child {
      margin-right: 0;
    }
    @media #{$breakpoint-md} {
      height: 112px;
    }
  }
  &__title {
    font-size: 21px;
    color: $black;
    font-family: $firagoMedium;
    margin-bottom: 34px;
  }
}

// collapse modal
.collapseModal {
  &-header {
    padding: 54px 0 35px;
  }
  &__title {
    font-family: $firagoMedium;
    font-size: 21px;
    color: $black;
    text-align: center;
    margin-bottom: 0;
    width: 100%;
  }
  &-body {
    padding: 40px 20px 30px;
  }
  &__clp {
    width: 100%;
    margin-bottom: 20px;
    border: 1px solid $component-color2;
    border-radius: 8px;
    padding: 10px 20px 0;
    &:last-child {
      margin-bottom: 0;
    }
    &.active {
      border: 2px solid $component-color2;
    }
    &__header {
      width: 100%;
      display: flex;
      font-family: $firagoMedium;
      font-size: 13px;
      color: $black;
      padding-bottom: 10px;
      &__img {
        height: 20px;
        flex: none;
        margin-right: 10px;
      }
    }
    &__body {
      text-align: left;
      border-top: 1px solid $component-color2;
      p {
        font-family: $firagoRegular;
        font-size: 16px;
        color: $component-color5;
        margin: 10px 0;
      }
    }
  }
}
